import { Button, Popover } from "@mui/material";
import React, { useContext } from "react";
import { UserContext } from "../App";

function TotalData() {
  const post = useContext(UserContext);

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const overallCalc = () => {
    let confirmed =
      post.length &&
      post.reduce((accumulator, curr) => accumulator + parseInt(curr.cases), 0);
    let recovered =
      post.length &&
      post.reduce(
        (accumulator, curr) => accumulator + parseInt(curr.recovered),
        0
      );
    let active =
      post.length &&
      post.reduce(
        (accumulator, curr) => accumulator + parseInt(curr.active),
        0
      );
    let deaths =
      post.length &&
      post.reduce(
        (accumulator, curr) => accumulator + parseInt(curr.deaths),
        0
      );

    return (
      <>
        <p>
          Confirmed:{" "}
          {confirmed &&
            confirmed.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
        </p>
        <p>
          Recovered:{" "}
          {recovered &&
            recovered.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
        </p>
        <p>
          Active:{" "}
          {active && active.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
        </p>
        <p>
          Deaths:{" "}
          {deaths && deaths.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
        </p>
      </>
    );
  };
  return (
    <div>
      <Button aria-describedby={id} variant="contained" onClick={handleClick}>
        Worldwide Data
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "top",
          horizontal: "left",
        }}
        transformOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <div className="popover">{overallCalc()}</div>
      </Popover>
    </div>
  );
}

export default TotalData;
