import React, { useState, useEffect, useContext, createContext } from "react";
import Graph from "./Graph";
import Menu from "./Menu";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Pagination,
  Select,
} from "@mui/material";
import { UserContext } from "../App";
import { Bar } from "react-chartjs-2";
import TotalData from "./TotalData";
export const GraphType = createContext(null);

function Home() {
  const [sortedData, setSortedData] = useState([]);
  const [sortedFlag, setSortedFlag] = useState("Country");
  const [page, setPage] = useState(1);
  const [graphType, setGraphType] = useState(Bar);
  const [chartData, setChartData] = useState();
  const post = useContext(UserContext);

  useEffect(() => {
    sortedData &&
      sortedData.sort(function (a, b) {
        return a[sortedFlag] < b[sortedFlag]
          ? -1
          : a[sortedFlag] === b[sortedFlag]
          ? 0
          : 1;
      });
    sortedData &&
      setChartData({
        labels: sortedData.map((cn) => cn.Country),
        datasets: [
          {
            label: "Deaths",
            data: sortedData.map((cn) => cn.Deaths),
            backgroundColor: "rgba(120, 120, 120, 0.5)",
          },
          {
            label: "Active",
            data: sortedData.map((cn) => cn.Active),
            backgroundColor: "rgba(0, 205, 70, 0.5)",
          },
          {
            label: "Recovered",
            data: sortedData.map((cn) => cn.Recovered),
            backgroundColor: "rgba(255, 99, 132, 0.5)",
          },
          {
            label: "Confirmed",
            data: sortedData.map((cn) => cn.Confirmed),
            backgroundColor: "rgba(53, 162, 235, 0.5)",
          },
        ],
      });
  }, [sortedData, sortedFlag]);
  console.log(sortedFlag);
  useEffect(() => {
    setSortedData(
      post.length &&
        post
          .map((cn) => ({
            Country: cn.country,
            Confirmed: cn.cases,
            Deaths: cn.deaths,
            Recovered: cn.recovered,
            Active: cn.active,
          }))
          .splice(page * 5 - 5, 5)
    );
  }, [post, page]);

  const PageChange = (event, value) => {
    setPage(value);
  };
  const handleChange = (e) => {
    setSortedFlag(e.target.value);
  };

  return (
    <>
      <GraphType.Provider value={{ graphType, setGraphType }}>
        <>
          <div className="chart-container">
            {chartData ? <Graph chartData={chartData} /> : <></>}
          </div>
          <div className="flex">
            <TotalData />

            <Pagination
              className="pagination"
              count={(post && Math.ceil(post && post.length / 5)) || 0}
              color="primary"
              variant="outlined"
              onChange={PageChange}
            />
            <FormControl sx={{ minWidth: 150 }}>
              <InputLabel>Sort by</InputLabel>
              <Select
                value={sortedFlag}
                onChange={handleChange}
                label="Sort by"
              >
                <MenuItem value={"Country"}>Country Name</MenuItem>
                <MenuItem value={"Deaths"}>Deaths</MenuItem>
                <MenuItem value={"Active"}>Active</MenuItem>
                <MenuItem value={"Recovered"}>Recovered</MenuItem>
                <MenuItem value={"Confirmed"}>Confirmed</MenuItem>
              </Select>
            </FormControl>
          </div>
          {graphType && <Menu />}
        </>
      </GraphType.Provider>
    </>
  );
}

export default Home;
