import React, { useState, useEffect, createContext } from "react";
import axios from "axios";
import "./App.css";
import Home from "./Components/Home";
import { Chart, registerables } from "chart.js";
import {
  Checkbox,
  createTheme,
  CssBaseline,
  Fab,
  ThemeProvider,
} from "@mui/material";
import WbSunnyOutlinedIcon from "@mui/icons-material/WbSunnyOutlined";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import toast, { Toaster } from "react-hot-toast";

export const UserContext = createContext(null);

function App() {
  const [mode, setMode] = useState("dark");

  const changeMode = () => {
    setMode((prev) => (prev === "dark" ? "light" : "dark"));
  };
  const theme = createTheme({
    palette: {
      mode: mode,
    },
  });
  Chart.register(...registerables);
  Chart.defaults.color = theme.palette.text.primary;
  Chart.defaults.scale.grid.color = theme.palette.divider;
  Chart.defaults.plugins.tooltip.borderColor = theme.palette.text.primary;

  // console.log(theme);
  console.log(Chart.defaults);
  const [post, setPost] = useState({});
  useEffect(() => {
    toast.loading("Fetching data...");
    axios
      // .get("https://api.covid19api.com/summary")
      .get("https://disease.sh/v3/covid-19/countries")
      .then((response) => {
        toast.dismiss();
        setPost(response.data);
      })
      .catch((err) => {
        toast.dismiss();
        toast.error("This didn't work.");
      });
  }, []);

  return (
    <UserContext.Provider value={post}>
      <ThemeProvider theme={theme}>
        <Toaster position="top-center" reverseOrder={false} />
        <Fab sx={{ position: "absolute", top: 16, right: 16 }}>
          <Checkbox
            icon={<WbSunnyOutlinedIcon style={{ color: "black" }} />}
            checkedIcon={<WbSunnyIcon style={{ color: "black" }} />}
            onChange={() => changeMode()}
          />
        </Fab>
        <CssBaseline />
        <Home />
      </ThemeProvider>
    </UserContext.Provider>
  );
}

export default App;
